/*
	 action creator
*/

// 
function requestAction(type) {
	return {
		type,
	}
}

// 
function receiveAction(type, data = {}) {
	return {
		type,
		payload: data,
	}
}

// 
function failAction(type, err) {
	return {
		type,
		payload: err
	}
}

const network = {
	requestAction,
	receiveAction,
	failAction,
}

export default network;


